import http from '@/components/utils/http.js';
export default {
	data() {
		return {
			info:[],
			serverapi:this.apiServer,
		}
	},
	
	onLoad(options) {
		this.getinfodisp(options.id)
	},
	methods: {
		
	getinfodisp(id){
		let	_that=this;
		let opts = {
			url: '/Productapi/findproductinfobyid/',
			method: 'post'
		};
		let param = {id:id};
		http.httpTokenRequest(opts, param).then(
			res => {
				//打印请求返回的数据
				if (res.data['code'] == 0) {
					_that.info=res.data['info'];
				} else {
					uni.showToast({ title: res.data.msg, icon: 'none' });
				}
			},
			error => {
				console.log(error);
			}
		);	
		},

	}
}
